class ErrorUser {
  status: number;

  errorMessage: string;

  constructor(response: Response) {
    this.status = response.status;
    this.errorMessage = '';
  }

  static getErrorMessage(statusCode: number, node: HTMLElement) {
    let message: string = '';
    if (statusCode === 400) {
      message = 'Bad request';
    }
    if (statusCode === 401) {
      message = 'Access token is missing or invalid';
    }
    if (statusCode === 404) {
      message = '   Email  ';
    }
    if (statusCode === 403) {
      message = '   ';
    }
    if (statusCode === 417) {
      message = '   Email  ';
    }
    node.innerHTML = message;
  }
}

export default ErrorUser;
